}

.CustomCheckbox_checkbox__-WVPU:checked ~ .CustomCheckbox_customSpan__3LJe1 {
    background-color: #00B4DB;
    border-radius: 5px;
    transform: rotate(0deg) scale(1);
    opacity:1;
    border: 2px solid rgb(216, 229, 241);
}

.CustomCheckbox_customSpan__3LJe1::after {
    position: absolute;
    content: "";
    left: 12px;
    top: 12px;
    height: 0px;
    width: 0px;
    border-radius: 5px;
    border: solid #009BFF;
    border-width: 0 3px 3px 0;
    transform: rotate(0deg) scale(0);
    opacity:1;
  	transition: all 0.3s ease-out;

}

.CustomCheckbox_checkbox__-WVPU:checked ~ .CustomCheckbox_customSpan__3LJe1::after {

    transform: rotate(45deg) scale(1);
    opacity:1;
    left: 8px;
    top: 3px;
    width: 6px;
    height: 12px;
    border: solid #f7f7f7;
    border-width: 0 2px 2px 0;
    background-color: transparent;
    border-radius: 0;
}

.CustomCheckbox_customSpan__3LJe1::before {
    position: absolute;
    content: "";
    left: 10px;
    top: 10px;
    width: 0px;
    height: 0px;
    border-radius: 5px;
    border: 2px solid #FFFFFF;
    transform: scale(0);
}

.CustomCheckbox_checkbox__-WVPU:checked ~ .CustomCheckbox_customSpan__3LJe1::before {
    left: -3px;
    top: -3px;
    width: 24px;
    height: 24px;
    border-radius: 5px;
    transform: scale(3);
    opacity:0;
    z-index: 999;
    transition: all 0.3s ease-out;

}
